@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital@1&family=Roboto:wght@100&display=swap");
body{
margin:0;
padding:0;
}
.container {

  background-color: #aacfe4;
  font-family: "Playfair Display", serif;
  font-family: "Roboto", sans-serif;
  overflow: hidden;
  height: 100vh;
  /* position: absolute; */

}

.container h1 {
  padding-top: 2rem;
margin-bottom: 2rem;
  color: #065d90;
  margin-left: 1rem;
}
.container h1 span {
  border-bottom: 2px solid #065d90;
  padding-bottom: 2px;
  display: inline;
}

.form {
  display: flex;
  justify-content: center;
}
.container form {
  display: flex;
  flex-direction: column;
  align-self: center;
  width: 40%;
  
}

.container form input,
.container form select  {
  height: 2rem;
  margin-bottom: 0.5rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border: none;
  border-radius: 5px;
}

.container textarea{
    margin-bottom: 0.5rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border: none;
    border-radius: 5px;
}

.container form button {
  align-self: center;
  width: 40%;
  height: 2.5rem;
  border: none;
  background-color: #065d90;
  color: white;
  margin: 2rem;
  cursor: pointer;
  font-size: larger;
  border: none;
  border-radius: 3px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.labelTravelCreation {
  color: #065d90;
  font-weight: bold;
  margin-bottom: 0.3rem;
}
::placeholder{
    opacity: 0.5;
    font-family:Arial, Helvetica, sans-serif !important;
    padding: 0.5rem;
    
}

@media screen and (max-width: 800px) {
.container form {
    width: 90%;
  }
.container h1 {
    font-size: large;
    margin-bottom: 2rem;
  }
}

/* animation part 
resourse used https://codepen.io/Lewitje/pen/BNNJjo
 */

bg-bubbles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.bg-bubbles li {
  position: absolute;
  list-style: none;
  display: block;
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.15);
  bottom: -160px;
  -webkit-animation: square 25s infinite;
  animation: square 25s infinite;
  -webkit-transition-timing-function: linear;
  transition-timing-function: linear;
}

.bg-bubbles li:nth-child(1) {
  left: 10%;
}

.bg-bubbles li:nth-child(2) {
  left: 20%;
  width: 80px;
  height: 80px;
  animation-delay: 2s;
  animation-duration: 17s;
}

.bg-bubbles li:nth-child(3) {
  left: 25%;
  animation-delay: 4s;
}

.bg-bubbles li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  animation-duration: 22s;
  background-color: rgba(255, 255, 255, 0.25);
}

.bg-bubbles li:nth-child(5) {
  left: 70%;
}

.bg-bubbles li:nth-child(6) {
  left: 80%;
  width: 120px;
  height: 120px;
  animation-delay: 3s;
  background-color: rgba(255, 255, 255, 0.2);
}

.bg-bubbles li:nth-child(7) {
  left: 32%;
  width: 160px;
  height: 160px;
  animation-delay: 7s;
}

.bg-bubbles li:nth-child(8) {
  left: 55%;
  width: 20px;
  height: 20px;
  animation-delay: 15s;
  animation-duration: 40s;
}

.bg-bubbles li:nth-child(9) {
  left: 25%;
  width: 10px;
  height: 10px;
  animation-delay: 2s;
  animation-duration: 40s;
  background-color: rgba(255, 255, 255, 0.3);
}

.bg-bubbles li:nth-child(10) {
  left: 90%;
  width: 160px;
  height: 160px;
  animation-delay: 11s;
}

@-webkit-keyframes square {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-500px) rotate(600deg);
  }
}

@keyframes square {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-500px) rotate(600deg);
  }
}


